import axios from 'axios'
import React from 'react'
import { useQuery } from 'react-query'
import Loader from '../loader/Loader'
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom'

export default function Brands() {
  function getAllBrands() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
  }
    const {isLoading , data , isError} = useQuery('Brands' , getAllBrands)
  if(isLoading) {
    return <Loader/>
  }
  const BrandsArry = data.data.data
  return <>
      <Helmet>
    <title>Brands</title>
    </Helmet>
  <div className='container py-4'>
    <div className="row gy-4">
        {BrandsArry.map((brand , index)=> {
          return <div key={index} className=" col-md-4 col-lg-3">
              <Link to={`${brand._id}`}>
                  <div className='bg-gray shadow main-border rounded-2 p-2 cursor-pointer'>
                        <img  src={brand.image} alt={brand.name} className='w-100 rounded-2' />
                        <h3 className='text-main text-center pt-2 fw-bold'>{brand.name} </h3>
                  </div>
              
              </Link>
      </div>
        })}
    </div>
  </div>
  </>
}
